@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;700&display=swap);
*,
*::after,
*::before {
	box-sizing: border-box;
}

html {
	scroll-behavior: smooth;
}

html,
body {
	margin: 0;
	padding: 0;
}

body {
	font-family: Raleway;
	background-color: #2b4162;
}

ul {
	list-style: none;
	padding: 0;
}

*::-moz-focus-inner,
*::-moz-focus-outer {
	border: 0;
}

::-webkit-scrollbar {
	display: none;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

/* Firefox */
input[type="number"] {
	-moz-appearance: textfield;
}

::selection {
	color: #2b4162;
	background-color: #fa9f42;
}

input,
select {
	outline: none;
}

.sorted {
	-webkit-animation: sorted 0.8s forwards;
	        animation: sorted 0.8s forwards;
	-webkit-animation-timing-function: ease-in-out;
	        animation-timing-function: ease-in-out;
}

@-webkit-keyframes sorted {
	0% {
		transform: scale(1);
	}
	50% {
		transform: scale(0.99);
	}
	75% {
		transform: scale(1.01);
	}
	100% {
		transform: scale(1);
	}
}

@keyframes sorted {
	0% {
		transform: scale(1);
	}
	50% {
		transform: scale(0.99);
	}
	75% {
		transform: scale(1.01);
	}
	100% {
		transform: scale(1);
	}
}

.card {
	background-color: #e0e0e2;
	border-radius: 0.5rem;
	width: 80%;
	margin: 1rem auto;
	padding: 0.5rem;
	box-shadow: 0.7rem 0.7rem 0.5rem rgba(0, 0, 0, 0.1);
}

.card:first-of-type {
	margin-top: 0;
}

.controls {
	text-align: center;
}

.controls input {
	margin-top: 1rem;
}

.controls .input-group {
	display: flex;
	flex-direction: column;
	position: relative;
}

.controls .input-group:not(:last-of-type) {
	margin-bottom: 1rem;
}

.controls .range-labels {
	display: flex;
	justify-content: space-between;
	font-size: 0.7rem;
}

.controls .range-labels p {
	margin: 0.5rem 0 0;
}

.controls .error-message {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -70%);
	opacity: 0;
	transition: all 0.3s;
	color: #721817;
	font-size: 0.7rem;
	width: 100%;
}

.controls .show-error {
	top: 70%;
	opacity: 1;
}

.change-num {
	display: flex;
	justify-content: space-around;
	align-items: center;
	width: 50%;
	margin-left: auto;
	margin-top: 1rem;
}

.increment,
.decrement {
	width: 20%;
	background-color: #2b4162;
	color: #e0e0e2;
	border-radius: 0.5rem;
	border: 2px solid #fa9f42;
	cursor: pointer;
	outline: none;
	padding: 0.2rem 0;
}

.controls input[type="number"] {
	text-align-last: center;
	font-family: inherit;
	border-radius: 0.5rem;
	border: 2px solid #fa9f42;
	color: #e0e0e2;
	background-color: #2b4162;
	width: 30%;
	padding: 0.25rem 0.5rem;
	margin-top: 0;
}

/* range styles stolen from https://codepen.io/joshbader/pen/MZMzjr */
.controls input[type="range"] {
	-webkit-appearance: none;
	-moz-appearance: none;
	     appearance: none;
	position: relative;
	width: 100%;
	height: 2em;
	padding: 0.375em;
	background-color: #2b4162;
	border-radius: 1em;
	outline: none;
	border: 1px solid #fa9f42;
	cursor: pointer;
}

.controls input[type="range"]::-webkit-slider-thumb {
	-webkit-appearance: none;
	appearance: none;
	/* position: relative; */
	z-index: 10;
	width: 1.5em;
	height: 1.5em;
	background-color: #fa9f42;
	border-radius: 50%;
	cursor: pointer;
}

.controls input[type="range"]::-moz-range-thumb {
	/* position: relative; */
	z-index: 10;
	width: 1.5em;
	height: 1.5em;
	background-color: #fa9f42;
	border: none;
	border-radius: 50%;
	cursor: pointer;
}

.controls input[type="range"]::-moz-range-track {
	background-color: transparent;
}

.bars {
	margin: 2rem 0;
	height: 300px;
	display: flex;
	justify-content: center;
	align-items: flex-end;
	position: relative;
}

select {
	width: 100%;
	font-family: inherit;
	text-align-last: center;
	border-radius: 2rem;
	border: none;
	padding: 1rem;
	background-color: #fa9f42;
	color: #e0e0e2;
}

select:-moz-focusring {
	color: transparent;
	text-shadow: 0 0 0 #e0e0e2;
}

option {
	text-align-last: center;
}

.select {
	width: 100%;
	background-color: #fa9f42;
	color: #e0e0e2;
	border-radius: 2rem;
	padding: 0.6rem 0 0rem;
	margin-bottom: 2rem;
	cursor: pointer;
}

.select h2 {
	margin: 0;
	font-weight: 400;
	position: relative;
}

.select ul {
	width: 100%;
	background-color: #721817;
	border-bottom-left-radius: 2rem;
	border-bottom-right-radius: 2rem;
	overflow: hidden;
	height: 16.5rem;
	transition: height 0.3s ease-out;
}

.select i {
	position: absolute;
	top: 5%;
	right: 5%;
	color: #721817;
}

.select ul li:not(:last-of-type) {
	border-bottom: 1px solid #829191;
}

.select ul li button {
	padding: 1rem 0;
	background-color: transparent;
	border: none;
	width: 100%;
	cursor: pointer;
	color: inherit;
	font-family: inherit;
	font-size: 1rem;
	transition: all 0.2s;
}

.select ul li button:hover {
	color: #fa9f42;
}

.select .collapsed {
	height: 0;
}

.invalid-inputs {
	position: absolute;
	top: 60%;
	left: 50%;
	transform: translate(-50%, -50%);
	transition: all 0.2s ease-out;
}

.invalid-inputs h1 {
	position: absolute;
	top: 27%;
	left: 50%;
	transform: translate(-50%, -50%);
	text-transform: uppercase;
	color: #721817 !important;
	font-weight: 700 !important;
	font-size: 2rem;
	width: 200% !important;
}

.svg {
	width: 100%;
	margin: auto;
}

/* .hide-invalid {
	top: 50%;
	opacity: 0;
} */

@media (max-width: 480px) {
	.invalid-inputs h1 {
		top: 30%;
		font-size: 1.7rem;
	}
}
@media (max-width: 413px) {
	.invalid-inputs h1 {
		top: 33%;
		font-size: 1.6rem;
	}
}
@media (max-width: 390px) {
	.invalid-inputs h1 {
		font-size: 1.5rem;
	}
}
@media (max-width: 368px) {
	.invalid-inputs h1 {
		font-size: 1.4rem;
	}
}
@media (max-width: 346px) {
	.invalid-inputs h1 {
		top: 36%;
		font-size: 1.3rem;
	}
}

.bar {
	display: inline-block;
	width: 20%;
	border-radius: 0.5rem;
	margin: 0.5rem 0.05rem;
	background-color: #577590;
	box-shadow: 0.3rem 0.5rem 0.5rem rgba(0, 0, 0, 0.2);
	display: flex;
	justify-content: center;
	align-items: flex-end;
}

.bar p {
	font-size: 0.8rem;
	/* font-size: 70%; */
	margin: 0 0 0.5rem;
	color: #e0e0e2;
	color: #2f3e46;
}

@media (max-width: 520px) {
	.bar p {
		font-size: 0.7rem;
	}
}
@media (max-width: 475px) {
	.bar p {
		font-size: 0.6rem;
	}
}
@media (max-width: 425px) {
	.bar p {
		font-size: 0.5rem;
	}
}
@media (max-width: 375px) {
	.bar p {
		font-size: 0.4rem;
	}
}

.button {
	padding: 0.5rem 1rem;
	color: #e0e0e2;
	background-color: #0b6e4f;
	border: none;
	font-family: inherit;
	font-size: 0.8rem;
	border-radius: 0.5rem;
	cursor: pointer;
	outline: none;
	margin: 0.5rem;
	transition: all 0.2s;
}

.button:hover {
	transform: translateY(-0.1rem);
	box-shadow: 0.1rem 0.1rem 0.5rem rgba(0, 0, 0, 0.2);
}

.button:active {
	transform: translateY(0);
	box-shadow: none;
}

.button:disabled {
	opacity: 50%;
	cursor: not-allowed;
}

.button.burgundy {
	background-color: #721817;
}

@media (hover: none) {
	.button:hover {
		transform: none;
		box-shadow: none;
	}
}




